.start-gray{background: url(../image/task/start-16x16-grey.png);width: 16px;height: 16px;display: inline-block;}

 
.footer{border-top: 1px solid #e6e6e6;height: 90px;}
.footer .txt{text-align: center;/*font-size: 14px;*/color: #666666;padding-top: 32px;font-family:Arial;}




/* 发布需求成功 */
.demand-success{padding: 20px 0 0 0;}
.demand-success .p1th{width: 100%;height: 80px;background-color: #d2fcd0;color: #0fb365;border-radius: 5px}
.demand-success .p1th .wbox{padding: 15px 0 0 25px;line-height: 24px;font-size: 14px;}
.demand-success .p1th .wbox .lbox{background: url(../image/task/icon-right-18x18.png);width: 18px;height: 18px;margin: 5px 15px 0 0}
.demand-success .p2th .title{font-size: 28px;padding: 40px 0 90px 0;font-weight: normal;}
.demand-success .p2th .list{}
.demand-success .p2th .list li{ float:left;display:inline;width: 240px;text-align: center;  }
.demand-success .p2th .list .lel{font-size: 60px;color: #5daaeb;}
.demand-success .p2th .list .num{font-size: 18px;color: #aaaaaa;padding-top: 50px;}
.demand-success .p2th .des{font-size: 18px;color: #666666;font-weight: normal;padding: 50px 0 190px 0;}

/* 不足币 */
.need-b-page{width: 380px;margin: 0 auto;}
.need-b-page .p1th{font-size: 28px;line-height: 40px;padding: 70px 0 50px 0;}
.need-b-page .p2th{margin-top: 10px;}
.need-b-page .p2th .buy{font-size: 18px;}
.need-b-page .p2th .s{font-size: 18px;color: #aaaaaa; text-decoration:line-through; }
.need-b-page .p2th .real{font-size: 18px;margin-left: 20px;}
.need-b-page .p2th .num{display: inline-block;padding: 3px 10px;background-color: #63cb76;color: #fff;border-radius: 5px;margin-left: 10px;}
.need-b-page .p3th{font-size: 16px;line-height: 26px;margin-top: 40px;}
.need-b-page .p3th .icon-r{display: inline-block;background: url(../image/task/icon-right-14x14.png);width: 14px;height: 14px;vertical-align: middle;margin-right: 5px;}
.need-b-page .btn{width: 180px;text-align: center;height: 40px;line-height: 40px;border-radius: 5px;background-color: #4092f1;cursor: pointer;font-size: 16px;color: #fff;margin-top: 60px;margin-bottom: 200px;}
.need-b-page .btn:hover{background-color: #66a8f4;}
.need-b-page .p2th select{width: 120px;height: 30px;font-size: 14px;padding-left: 10px;}
.need-b-page .p2th .need-money{font-size: 18px;margin-top: 30px;}
.need-b-page .p2th .alipay{font-size: 18px;position: relative;margin-top: 25px;}
.need-b-page .p2th .alipay img{vertical-align: middle;border: 1px solid #4092f1;border-radius: 3px}



/* 提供报价 */
.provide-price-page{padding-bottom: 190px;}
.provide-price-page .p1th{padding: 35px 0;}
.provide-price-page .p1th .lbox{font-size: 28px;}
.provide-price-page .p1th .rbox{border-radius: 5px;width: 65px;height: 34px;line-height: 34px;text-align: center;border: 1px solid #aeaeae;cursor: pointer;font-size: 14px;
    background:-webkit-gradient(linear, 0 0, 0 100%, from(#fcfcfc), to(#e9e9e9));
    background:-moz-linear-gradient(top, #fcfcfc,#e9e9e9);
}
.provide-price-page .p1th .rbox:hover{
    background:-webkit-gradient(linear, 0 0, 0 100%, from(#fafafa), to(#e2e0e0));
    background:-moz-linear-gradient(top, #fafafa,#e2e0e0);
}
.provide-price-page .sidebar{width: 350px;}
.provide-price-page .sidebar .line{height: 4px;overflow: hidden;background-color: #ff6763;}
.provide-price-page .sidebar .icon-b{background: url(../image/task/icon-baojia-20x20.png);}

.provide-price-page .sidebar .user-area{}
.provide-price-page .sidebar .user-area .name{font-size: 20px;color: #333333;padding: 10px 0 0 15px;}
.provide-price-page .sidebar .user-area .img img{border-radius: 50%;}
.provide-price-page .sidebar .item-wrap{color: #666;margin-top: 20px;}
.provide-price-page .sidebar .item-wrap h3{}
.provide-price-page .sidebar .item-wrap .p1{padding-top: 5px;}

.provide-price-page .sidebar .item-wrap .item{border-bottom: 1px solid #e6e6e6;padding: 15px 0px;}
.provide-price-page .main-part{width: 500px;background-color: #f2f2f2;padding: 0 30px 30px 30px;}
.provide-price-page .main-part .send-area{width: 100%;background-color: #fff;padding-top: 20px;padding-bottom: 20px;margin-top: 20px;margin-bottom: 20px;}
.provide-price-page .main-part .send-area .lbox{padding:0 18px 20px 18px;}
.provide-price-page .main-part .send-area .lbox img{max-width: 60px; border-radius: 40px;}
.provide-price-page .main-part .send-area .txt-area{}
.provide-price-page .main-part .send-area .txt-area .textarea-class{width: 380px;height: 110px;padding: 10px;background-color: #fff;border: 1px solid #cccccc;border-radius: 5px;font-size: 14px;}
.provide-price-page .main-part .send-area .btn{width:70px;text-align: center;height: 40px;line-height: 40px;border-radius: 5px;background-color: #4092f1;cursor: pointer;font-size: 16px;color: #fff;margin-top: 15px;}
.provide-price-page .main-part .send-area .btn:hover{background-color: #66a8f4;}
.provide-price-page .state-area{}
.provide-price-page .state-area{width: 100%;background-color: #fff;padding-top: 20px;padding-bottom: 20px;}
.provide-price-page .state-area .lbox{padding:0 18px 20px 18px;}
.provide-price-page .state-area .lbox img{max-width: 40px; border-radius: 40px;max-height: 40px;}
.provide-price-page .state-area .txt-area{width: 405px;color: #666666;}
.provide-price-page .state-area .txt-area .box1{color: #aaaaaa;font-size: 12px;}
.provide-price-page .state-area .txt-area .p2{padding: 5px 0 1px 0;}
.provide-price-page .state-area .txt-area .p3{padding: 5px 0;color: #4092f1;}
.provide-price-page .state-area .txt-area .p5{word-break: break-all;width: 100%;}

.provide-price-page .state-line{border-left: 4px solid #fff;padding: 12px 0 12px 70px;background-color: #fff;color: #666666;}
.provide-price-page .state-line .lbox{width: 300px;line-height: 22px;}
.provide-price-page .state-line .rbox{color: #aaaaaa;padding: 2px 20px 0 0;}
.provide-price-page .get-back-money{border-color: #63cb76;background-color: #eefeed}
.provide-price-page .see-you-price{border-color: #5daaeb;background-color: #e6f1fd}
.provide-price-page .no-use-you{border-color: #9ba5b1;background-color: #ebebeb}
.provide-price-page .pay-money{border-color: #fe9920;background-color: #ffe6c8}
.provide-price-page .sidebar .has-use-you{font-size: 20px;line-height: 26px;padding: 30px;border-top: 4px solid #5daaeb;}
.provide-price-page .msg-chat{padding: 20px 0 10px 0;border-bottom: 1px solid #e6e6e6}

/* 分页 */
.page-mod{text-align: center;font-size: 12px;padding-top: 50px;color: #666666;}
.page-mod a{margin-right: 10px;color: #666666;}
.page-mod a.click{font-weight: bold;}









